*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    
    
}
.app{
    min-width: 628px ;
    max-width: 628px;   
}
h1{
    font-size: 80px;
     color: Black;
     width: 100%;
     text-align: center;
 }
 button{
    border:none;
    background-color:transparent;
    outline:none;
 }
 .main{
     display: flex;
     justify-content: center;
 
 }
 .list{
     display: none;
     list-style-type: none;
 }
 .task-container{
     display: flex;
     align-items: center;
     justify-content: center;
     gap: 20px;
     margin-top: 30px;
 }
 .task-heading{
     float: left;
     font-size: 40px;
     text-align: right;
 }
 .todo-container{
    padding: 100px;
    width: 100%;
    
    

 }   
 .todo{ /* each individual todo, entire line*/
    display:flex;
    justify-content: space-between;
    padding: 4px 8px;
     
 }
 .todo:last-child{
     align-items: center;
 }
 .todo-item{
    width:300px;
    min-width: 290px;
    list-style: none;
    padding: 20px 0px 10px;
    font-size: x-large; 
    overflow-wrap:break-word;
    
 }
 
 .btn{ /*submit button*/
  background-color: #4CAF50; /* Green */
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  border-radius: 10px;
 }

 .btn:hover{
    background-color: #0d791b;
 }
.todo-btn{ /*check/delete button div*/
    margin-top: 10px;
}
   
.complete-btn{
    padding-right: 10px;
    border: black;
}
.complete-btn:hover{
   color: #4CAF50;
}

.trash-btn:hover{
    color: rgb(216, 1, 1);
    
 }
.finished-btn{
  background-color: rgb(216, 1, 1);
}
.finished-btn:hover{
    background-color: rgb(151, 1, 1)
}
.finished-container{
    width: 100%;
    display: flex;
    justify-content: center;
    padding: 30px;
}
 
.text-field{
    border-style: none;
    border: 2px solid grey;
    border-radius: 5px;
    padding: 15px 32px;
    font-size: 16px;
    
}
 .strike-through{
     text-decoration: line-through;
     
 }
 .strike-th{
    background-color: #ff00003d;
    border-radius: 10px;
 }